import './gallery.css'

function makeElement (meteorFile) {
    return <figure className='gallery_item'><a href={'/main/images/astrophotography/meteor/'+meteorFile+'.jpg'}><img src={'/main/images/astrophotography/meteor/'+meteorFile+'.jpg'}/></a></figure>
}

