                    {makeElement('2019_12_15_LRPT_16-16_vis+ir')}
                    {makeElement('2019_12_16_LRPT_15-37-38_ir')}
                    {makeElement('2019_12_16_LRPT_15-37-38.s-vis')}
                    {makeElement('2019_12_19_LRPT_22-29-00')}
