import './navigation.css'
import { Link } from 'react-router-dom'
/*
<li key='videos'><Link to="/videos">Видео</Link></li>
<li key='articles'><Link to="/articles">Статьи</Link></li>
<li key='articles'><Link to="/articles">Покрытия</Link></li>
*/
 


function showNav(){
    let ulState=document.querySelector('ul').style
    ulState.display==='inherit'?ulState.display='none':ulState.display='inherit'
}

const Navigation = () => {
    return (
    <nav>
        <button onClick={showNav}>РАЗДЕЛЫ [скрыть/показать]</button>
        <ul>
            <li><Link to="/main/">Начальная</Link></li>
            <li>Фотогалерея:
                <ul>
                    <li key='sun'><Link to="/main/sun">Солнце</Link></li>
                    <li key='moon'><Link to="/main/moon">Луна</Link></li>
                    <li key='planets'><Link to="/main/planets">Планеты</Link></li>
                    <li key='comets'><Link to="/main/comets">Кометы</Link></li>
                    <li key='dso'><Link to="/main/dso">Объекты глубокого космоса</Link></li>
                    <li key='atmospheric'><Link to="/main/atmospheric">Атмосферные явления</Link></li>
                </ul>
            </li>
            
            <li>Радио:
                <ul>
                    <li key='noaa'><Link to="/main/noaa">Метеоснимки NOAA</Link></li>
                    <li key='meteor'><Link to="/main/meteor">Метеоснимки "Метеор"</Link></li>
                    <li key='issham'>Переговоры с МКС</li>
                    <li key='sstv'><Link to="/main/sstv">SSTV с МКС</Link></li>
                </ul>
            </li>
            
            <li key='calc'><Link to="/main/calc">Калькуляторы</Link></li>
            <li key='spaceweather'><Link to="/main/spaceweather">Солнечная активность</Link></li>
            <li key='links'><Link to="/main/links">Полезные ссылки</Link></li>
        </ul>
    </nav>)
}

export default Navigation