import './gallery.css'

function makeElement (elementName, date, animation) {
    return <figure className='gallery_item'><a href={'/main/images/astrophotography/noaa/'+elementName+'.jpg'}><img src={'/main/images/astrophotography/noaa/'+elementName+'_min.jpg'}/></a><figcaption>{date}</figcaption>
    {animation?(<figcaption><a href={'/main/images/astrophotography/noaa/'+elementName+'.gif'} target='_blank'>Смотреть анимацию</a></figcaption>):''}
</figure>
}

const Noaa = () => {
    return (
        <section>
            <h2>Приём снимков с метеоспутников группировки NOAA</h2>
            <p></p>
            <h2>2019 год</h2>
            <div className='gallery_grid'>
                {makeElement('2019_08_02', '2 августа')}
                {makeElement('2019_08_24', '24 августа', true)}
                {makeElement('2019_08_25', '25 августа')}
                {makeElement('2019_08_26', '26 августа')}
                {makeElement('2019_08_27', '27 августа')}
                {makeElement('2019_08_29', '29 августа')}
                {makeElement('2019_08_30', '30 августа')}
                {makeElement('2019_08_31', '31 августа', true)}
                {makeElement('2019_09_01', '1 сентября', true)}
                {makeElement('2019_09_08', '8 сентября', true)}
                {makeElement('2019_09_29', '29 сентября', true)}
                {makeElement('2019_11_23', '23 ноября', true)}
                {makeElement('2019_12_08', '8 декабря', true)}
                {makeElement('2019_12_19', '19 декабря', true)}
            </div>
        </section>
    )
}

export default Noaa