import './gallery.css'

function makeElement (sstvFile) {
    return <figure className='gallery_item'><a href={'/main/images/astrophotography/ariss/'+sstvFile+'.jpg'}><img src={'/main/images/astrophotography/ariss/'+sstvFile+'.jpg'}/></a></figure>
}

const Sstv = () => {
    return (
        <section>
                <h2>Приём изображений SSTV с борта Международной Космической Станции</h2>
                <p className='category_desc'>На борту Международной космической станции установлено любительское радио. В рамках различных экспериментов осуществляется передача изображений и текста по радиолюбительскому каналу связи на разную тематику. Передача осуществляется в автоматическом режиме на частоте 145.800 MHz. Принять данные могут радиолюбители почти из любой точки Земли. По результатам экспериментов любителей награждают дипломами.</p>
                <p className='category_desc'>На этой страничке собраны все мои результаты.</p>
                <hr></hr>
                <h2>Интер-МАИ-75 SSTV: 30-31 января 2020 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202001301620a')}
                    {makeElement('202001301758')}
                </div>
                <hr></hr>
                <h2>Интер-МАИ-75 SSTV: 30 сентября - 1 октября 2020 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202009301904')}
                    {makeElement('202009301908')}
                    {makeElement('202010011614')}
                    {makeElement('202010011744')}
                </div>
                <hr></hr>
                <h2>SSTV-эксперимент памяти запуска первого спутника: 3 - 4 и 6 - 8 октября 2020 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202010032127')}
                    {makeElement('202010041414')}
                    {makeElement('202010041419')}
                    {makeElement('ariss_2020oct')}
                </div>
                <hr></hr>
                <h2>SSTV-эксперимент «20-летие радиолюбительства на МКС»: 24 - 31 декабря 2020 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202012270558')}
                    {makeElement('202012270730')}
                    {makeElement('202012270906')}
                    {makeElement('ariss_2020dec')}
                </div>
                <hr></hr>
                <h2>Интер-МАИ-75 SSTV: 28 и 29 января 2021 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202101281816')}
                </div>
                <hr></hr>
                <h2>Интер-МАИ-75 SSTV: 1 - 2 декабря 2021 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202112011644')}
                    {makeElement('202112011658')}
                    {makeElement('202112011811')}
                </div>
                <hr></hr>
                <h2>SSTV-эксперимент на тему «Исследование Луны»: 26 - 31 декабря 2021 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202112300729')}
                    {makeElement('ariss_2021dec')}
                </div>
                <hr></hr>
                <h2>Интер-МАИ-75 SSTV: 7 - 8 февраля 2022 года</h2>
                <div className='gallery_grid'>
                    {makeElement('202202072007')}
                    {makeElement('202202072012')}
                </div>
        </section>
    )
}

export default Sstv